.BuildControls {
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  margin: auto;
  padding: 10px 0;
  border: 1px solid #eee;
  -webkit-box-shadow: 0 2px 3px #ccc;
          box-shadow: 0 2px 3px #ccc;
  background: #fff;
}

.OrderButton {
    background-color: var(--orange-lighter);
    outline: none;
    cursor: pointer;
    border: 1px solid var(--orange-darker);
    color: var(--orange-darker);
    font-family: inherit;
    font-size: 1.2em;
    margin: 30px;
    padding: 15px 30px;
    -webkit-box-shadow: 2px 2px 2px var(--orange-darker);
            box-shadow: 2px 2px 2px var(--orange-darker);
}

.OrderButton:hover, .OrderButton:active {
    background-color: var(--orange);
    border: 1px solid var(--orange-darker);
    color: var(--orange-darker);
}

.OrderButton:disabled {
    background-color: var(--orange-lighter);
    cursor: not-allowed;
    border: 1px solid var(--orange);
    color: #888;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


